.--color-nav-blue {
  color: $color-dark-state-blue;
}

.--font-extrabold {
  font-weight: 800;
}

.--font-bold {
  font-weight: 700;
}

.--font-semibold {
  font-weight: 600;
}

.--font-medium {
  font-weight: 500;
}

.h2-title {
  margin: 0;
  margin-bottom: 1em;
  font-weight: 700;
  font-size: 3rem;
  line-height: 140%;
  color: $color-dark-state-blue;

  @include bp-700 {
    font-size: 2.5rem;
  }
}

.btn {
  position: relative;
  display: inline-block;
  padding: 2.4em 1.75em 1.75em;

  &--main {
    border-radius: 15px;
    box-shadow: 0 7px 0 #3841c8, inset 0 -18px 30px #5158be, inset 0 22px 30px rgb(255 255 255 / 35%);
    font-weight: 800;
    font-size: 20px;
    line-height: 29px;
    letter-spacing: 0.08em;
    text-transform: uppercase;
    color: $color-default-white;
    background-color: #3743f9;
    transition: background-color 0.3s ease, box-shadow 0.3s ease;

    &::after {
      content: "";
      position: absolute;
      left: 50%;
      right: 0;
      bottom: -15px;
      border-radius: 15px;
      width: 90%;
      height: 30px;
      background-color: rgb(84 95 250 / 20%);
      filter: blur(15px);
      transform: translateX(-50%);
    }

    &:hover {
      box-shadow: 0 7px 0 #3841c8, inset 0 -18px 30px #5158be, inset 0 22px 30px rgb(255 255 255 / 35%);
      background-color: #3d46c1;
    }

    &:active {
      box-shadow: inset 0 -19.218px 32.0301px #5158be, inset 0 23.4887px 32.0301px rgb(255 255 255 / 35%);
      background-color: #3743f9;
    }

    @include bp-425 {
      font-size: 16px;
    }

    @include bp-375 {
      font-size: 14px;
    }
  }
}

.bg-text {
  border-radius: 10px;
  padding: 0.8em 1.5em;
  background: #f5f6ff;
}

.big-text {
  font-size: 1.3em;
}

.tooltip {
  margin-left: 10px;

  &__btn {
    @include flex-all-center;

    border: 1px solid #a4a4a4;
    border-radius: 100%;
    width: 18px;
    height: 18px;
    font-weight: 400;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 0.01em;
    color: #a4a4a4;
    background-color: transparent;

    &:focus {
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
      }
    }

    @include hover {
      + .tooltip__txt {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__txt {
    border-radius: 5px;
    padding: 15px 20px;
    box-shadow: 0 2px 8px rgb(20 16 36 / 10%);
    font-weight: 400;
    font-size: 16px;
    line-height: 160%;
    text-align: center;
    color: var(--dark-color);
    background: var(--light-color);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}
